<template>
  <b-container class="bv-example-row mt-3 createJoinRoomBox">
    <b-row>
      <b-col md="2" lg="3"></b-col>
      <b-col md="8" lg="6">
        <b-form @submit.prevent="joinRoom()">
          <b-input-group v-if="showjoinRoomBox">
            <b-input-group-prepend>
              <b-button @click="togglejoinRoomBox()" variant="warning">Cancel</b-button>
            </b-input-group-prepend>
            <b-form-input
              spellcheck="false"
              autocomplete="off"
              maxlength="250"
              v-model="roomId"
              :state="roomId.trim().length ? true : false"
              placeholder="Paste your Room Id"
              trim
            ></b-form-input>
            <b-input-group-append>
              <b-button @click="joinRoom()" variant="info">Join</b-button>
            </b-input-group-append>
          </b-input-group>
          <b-button class="createNdJoinButton" @click="togglejoinRoomBox()" v-else variant="warning">Join Room</b-button>
        </b-form>
      </b-col>
      <b-col md="2" lg="3"></b-col>
    </b-row>
  </b-container>
</template>
<script>
import store from "@/store";
import Server from "@/server";

export default {
  computed: {
    showjoinRoomBox: () => store.state.room.showjoinRoomBox
  },
  methods: {
    togglejoinRoomBox: () => {
      store.state.room.showjoinRoomBox = !store.state.room.showjoinRoomBox;
      store.state.room.showCreateRoomBox = false;
    },
    joinRoom: function() {
      var roomId = this.roomId.trim().replace(/ /g,'');
      if (!roomId.length) return;
      this.togglejoinRoomBox();
      if(!isNaN(roomId)){
        Server.joinBingoRoomByAlias(roomId,true);
      }else{        
        if(roomId.indexOf('room')>=0){
            roomId = roomId.replace(/.*room\//g,'');
            roomId = roomId.replace(/\/.*/g,'');
        }
        Server.joinBingoRoom(roomId,true);
      }
    }
  },
  data: function() {
    return {
      roomId: ""
    };
  }
};
</script>
